<script lang="js">
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'Presentation',
    mounted() {
        document.getElementById("mainCard").addEventListener("mouseover", this.displayCardsFunc);
    },
    data() {
        return {
        }
    },
    methods: {
        displayCardsFunc(ev) {
        let el = document.getElementsByClassName("card");
        Array.from(el).forEach(element => {
            element.style.transform = "translateY(-50px)";
            element.style.opacity = "1";
            element.style.transition = "ease-in-out .5s, opacity .5s";
        });
        document.getElementById("mainCard").removeEventListener("mouseover", this.displayCardsFunc, false);
        }
  }
    
});
</script>