<script lang="js">
import { getUserInfo } from './userService.js'
import Switcher from './components/Switcher'

export default {
  name: "area-navbar",
  created() {
    let userName = localStorage.getItem('Name')
    window.addEventListener('scroll', this.hideNavbar);
    if (localStorage.getItem('token')) {
      this.isLogged = true;
      getUserInfo().then((res) => {
        this.userName = res.data.Name;
      });
    }
  },
  data() {
    return {
      showMenu: false,
      scrollPos: window.pageYOffset,
      isLogged: false,
      userName: "",
    }
  },
  methods: {
    toggleNavbar: function () {
      this.showMenu = !this.showMenu;
    },
    hideNavbar(ev) {
      var currentScrollPos = window.pageYOffset;
      if (this.scrollPos > currentScrollPos) {
        document.getElementById("header").style.top = "0";
      } else {
        document.getElementById("header").style.top = "-80px";
      }
      this.scrollPos = currentScrollPos;
    },
    logout() {
      console.log("User LogOut")
      localStorage.removeItem('access_token');
      window.location.reload();
    },
    truncate(data) {
      if (data.length > 15)
        data = data.substring(0, 15).concat('...');
      return (data);
    },
    scrollContact() {
      // const pos = document.getElementById('Contact-page').offsetTop;
      // console.log(pos);
      // window.scrollTo({top: pos, behavior: "smooth"});
      this.$router.push({ name: 'ContactUs' });
    }
  },
  components: {
    Switcher
  }
}
</script>