<script lang="js">
import { defineComponent } from 'vue';
import { useAuthStore } from "../../store/auth-store";
import { login, loginGoogle } from '../../services/auth-service.js';
import { inject } from "vue";

import vue3GoogleLogin from 'vue3-google-login'

export default defineComponent({
    name: 'Login',
    data() {
        return {
            email: '',
            password: '',
            alert: '',
            user: '',
            incorrect: false
        };
    },
    methods: {
        fetchUser() {
            login(this.email, this.password).then((res) => {
                console.log("User: " + this.email + " logged");
                console.log(res);
                localStorage.setItem("access_token", res.data.access_token);
                this.authStore.update(true);
                this.$emit('login', true);
                window.location.href = "/"
            }).catch((error) => {
                console.log("Wrong Password or UserName");
                this.incorrect = true;
                this.alert = error;
                window.location.href = "/"
            });
        },

        async handleClick() {
            if (this.$route.query.token) {
                localStorage.setItem('accessToken', this.$route.query.token);
                localStorage.setItem('userId', this.$route.query.id);
                this.$router.push('/')
            }
        }
    },
})
</script>

