                <div class="login-card">
                    <h1 class="login-title">{{ $t('sign.login') }}</h1>
                    <input class="input" :placeholder="$t('sign.email')" type="text" name="email" id="email"
                        v-model="email">
                    <input class="input" :placeholder="$t('sign.password')" type="password" name="password" id="password"
                        v-model="password">

                    <!-- ERROR pendiente de resolver -->
                    <!-- <p v-if="incorrect == true" class="wrong">{{ $t('errors.wrongpass') }}</p> -->

                    <button class="login-button" @click="fetchUser">{{ $t('sign.login2') }}</button>
