<template>
    <div div class="modal">
        <input class='inp' placeholder="enter your email" type="text" name="email" id="email" v-model="email">
        <button @click="closeModal" class="send">Send</button>
    </div>
</template>

<script lang="js">
import { defineComponent } from 'vue';
import {earlyAccess} from '../../services/auth-service.js';
export default defineComponent({
    name: 'Modal',
    created() {
    },
    data() {
        return {
            show: false,
            email: '',
        }
    },
    methods: {
        closeModal: function() {
            if (this.email != '') {
                earlyAccess(this.email);
            }
            this.$emit('show', this.show);
        }
    }
    
});
</script>

<style scoped>
.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: 1em;
}

.send {
 background-color: #272727;
 color: white;
 padding: 5px 25px;
 border-radius: 10px;
}

.inp {
 border: 1px solid;
 border-radius: 5px;
 margin-bottom: 10px;
}
</style>