<template>
    <div class="platform">
        <router-link to="/admin" class="admin">Admin Panel</router-link>
        <div class="empty" v-if="projects.length == 0">
            <h1>you do not have any projects</h1>
            <button @click="createProject" class="create">Create Project</button>
        </div>
        <div class="projects" v-if="projects.length > 0">
            <div class="container" v-for="(project, index) in projects">
                <Project 
                :name="project.name"
                :desc="project.desc"
                :type="project.type"
                :model="project.model"
                :edit="project.edit"
                :uuid="project.uuid"
                v-on:edit="changeEdit(index)">                
                </Project>
            </div>
        </div>
        <button v-if="projects.length > 0" @click="createProject" class="create">Create Project</button>
        <camera :resolution="{ width: 1920, height: 1080 }" ref="camera" autoplay></camera>
        <div class="snapshot-button">
            <button class="snapshot" @click="snapshot">Create snapshot</button>
        </div>
    </div>
</template>
  
<style>
.snapshot {
    background-color: #0049b0;
    color: white;
    padding: 10px 40px;
}
video {
    border: 1px solid black;
    width: 500px !important;
    height: fit-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    margin-bottom: 50px;
}
.container {
    width: fit-content;
    margin-right: 30px;
    margin-bottom: 20px;
}
.projects {
    padding-top: 50px;
    margin-left: 30px;
    display: flex;
    align-items: flex-start;
    overflow-x: scroll;
}
.platform {
    background-color: #EEE;
    width: 100%;
    height: 100%;
}
.empty {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50%;
    flex-direction: column;
}
.empty > h1 {
    font-size: 30px;
    color: #0049b0;
    font-weight: 600;
}
.create {
    margin-top: 50px;
    background-color: #0049b0;
    border-radius: 0.1em;
    padding: 10px 40px;
    color: white;
    transition: all 0.5s;
}
.create:hover {
    background-color: black;
}

.admin {
    display: inline-block;
    margin-top: 70px;
    background-color: #0049b0;
    border-radius: 0.1em;
    padding: 10px 40px;
    color: white;
    transition: all 0.5s;
}
.admin:hover {
    background-color: black;
}

.snapshot-button {
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (max-width: 760px) {
.projects {
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
}

}

</style>
  
<script lang="js">
import { defineComponent, ref } from 'vue';
import Project from '../components/Platform/Project';
import Camera from "simple-vue-camera";
import {getProj} from "../userService.js";

export default defineComponent({
    name: 'Platform',
    data() {
        return {
            projects: [],
        };
    },
    setup() {
        // Get a reference of the component
        const camera = ref();

        // Use camera reference to call functions
        const snapshot = async () => {
            console.log("snap")
            const blob = await camera.value?.snapshot();

            // To show the screenshot with an image tag, create a url
            const url = URL.createObjectURL(blob);
            console.log(url)
        }

        return {
            camera,
            snapshot
        }
    },
    components: {
        Project,
        Camera
    },
    mounted() {
        getProj().then((res) => {
            for (const proj of res.data) {
                console.log(proj)
                this.projects.push({name: proj.name, desc: proj.description, type: 0, model: proj.models[0].uuid, edit: false, uuid: proj.uuid})
            }
        })
        .catch((error) => {
            this.incorrect = true;
            this.alert = error;
        });
    },
    methods: {
        createProject: function () {
            this.projects.push({name: '', desc: '', type: 0, model: "", edit: true, uuid: ""});
        },
        changeEdit: function (index) {
            this.projects[index].edit = !this.projects[index].edit;
        },
    }
})
</script>