            />
          </div>
        </div>

        <div class="field">
          <label class="label"></label>
          <div class="control has-icons-left has-icons-right">
            <input
              class="input is-danger"
              type="email"
              placeholder="Email"
              v-model="email"
            />
            <span class="icon is-small is-left">
              <i class="fas fa-envelope"></i>
            </span>
            <span class="icon is-small is-right">
              <i class="fas fa-exclamation-triangle"></i>
            </span>
          </div>
        </div>

        <div class="field">
          <label class="label">{{ $t("contactUs.form.subject") }}</label>
          <div class="control">
            <div class="select">
              <select v-model="subject">
                <option>{{ $t("contactUs.form.select") }}</option>
                <option>{{ $t("contactUs.form.suggestion") }}</option>
                <option>{{ $t("contactUs.form.feedBack") }}</option>
