          <div class="control">
            <label class="checkbox">
              <input type="checkbox" v-model="agree" />
              {{ $t("contactUs.agree") }}
              <a href="#">{{ $t("contactUs.termsConditions") }}</a>
            </label>
          </div>
        </div>

        <button @click="handleSignIn" class="google-contactUs" :disabled="isSubmitDisabled">
          {{ $t("contactUs.submit") }}
        </button>
