<script lang="js">
import { defineComponent } from 'vue';
import VueFaq from './VueFaqAccordion.vue'
import axios from 'axios';

export default defineComponent({
  data() {
    return {
      name: '',
      email: '',
      subject: '',
      description: '',
      agree: false,
      requiredFields: ['name', 'email', 'subject', 'description']
    };
  },
  methods: {
    handleSignIn() {
      const formData = {
        email: this.email,
        fields: {
          name: this.name,
          email: this.email,
          subject: this.subject,
          description: this.description,
          // agree: false
        },
        groups: [
          '88874904458364655' // ID for contact us group
        ]
      };

      console.log('Nombre:', this.name);
      console.log('Email:', this.email);
      console.log('Asunto:', this.subject);
      console.log('Descripción:', this.description);
      console.log('Acepto los términos y condiciones:', this.agree);
      
      const endpoint = 'https://connect.mailerlite.com/api/subscribers';
      const authToken = process.env.VUE_APP_AUTH_TOKEN;
      
      
      axios.post(endpoint, formData, {
        headers: {
          'Authorization': `Bearer ${authToken}`
        }
      }).then(response => {
        console.log('API response:', response.data);
      }) .catch(error => {
        console.error('API error:', error.response.data);
      });
    }
  },computed: {
    isSubmitDisabled() {
      for (const field of this.requiredFields) {
        if (!this[field]) {
          return true; 
        }
      }
      return false;
    }
},
  components: {
    VueFaq
  }
});
</script>
