<script lang="js">
  import { defineComponent } from 'vue';
  import { register } from '../../services/auth-service.js';
  import { useAuthStore } from "../../store/auth-store";
  import { loginGoogle } from '../../services/auth-service.js';
  import { inject } from "vue";

  export default defineComponent ({
    name: 'Register',
    data () {
      return {
        email: '',
        password: '',
        confirmPassword: '',
        firstname: '',
        lastname: '',
        alert: "",
        user: '',
        incorrect: false
      };
    },
    methods: {
      registerUser () {
        if (this.password === this.confirmPassword) {
          register(this.user, this.email, this.password).then((res) => {
          }).catch((er) => {
            this.incorrect = true;
            console.log(er)
          });
        }
        
        //this.alert = "";
        //if ((this.password === this.confirmPassword) && this.password != '' && this.email != '' && this.firstname != '' && this.lastname != '') {
        //  register(this.firstname, this.lastname, this.email, this.password).then((response) => {
        //      window.location.href = "/login";
        //  }).catch((error)=>{
        //      this.alert = error;
        //  });
        //}
        //else {
        //  this.alert = "Passwords must match";
        //}
      },
      printuser() {
        console.log(this.user);
      },
      async handleSignIn() {
        try {
          const googleUser = await this.$gAuth.signIn();
          // console.log(this.$gAuth.signIn);
          if (!googleUser) {
            return null;
          }
          this.user = googleUser.getBasicProfile().getEmail();
          console.log(this.user);
          loginGoogle(this.user).then((res) => {
            console.log(this.user);
            console.log(res.data.id);
            localStorage.setItem("userId", res.data.id);
            this.authStore.update(true);
            window.location.href = "/reactivity";
          })
          .catch((error) => {
            this.alert = error.response.data.message;
          });;
        } catch (error) {
          console.log(error);
          return null;
        }
        
      },
    },
    setup() {
      const Vue3GoogleOauth = inject("Vue3GoogleOauth");
      const authStore = useAuthStore();
      return {
        authStore,
        Vue3GoogleOauth,
      };
    },
    created() {
      console.log(localStorage.getItem("userId"));
    }
  })
</script>

