<template>
    <div class="project">
        <div v-if="edit == true">
            <p class="title">Project Name*</p>
            <input class='input' type="text" v-model="projectName">
            <p class="title">Description</p>
            <input class='input' type="text" v-model="projectDescription">
            <p class="title">Input Type*</p>
            <div class="type">
                <button :class="image" @click="changeType">Image</button>
                <button :class="video" @click="changeType">Video</button>
            </div>
            <p class="title">Models*</p>
            <div class="models">
                <input type="text" placeholder="Search Model" class="input" v-model="search" @change="filterModels">
                <div class="select-models">
                    <button v-for="(model, index) in selectedModels" @click="selectModel(index)" :class="model.selected" @mouseover="showDescription(index)" @mouseout="hideDescription(index)">
                        {{ model.name }}
                        <div v-if="hoveredIndex === index" class="description-box">
                            {{ model.desc }}
                        </div>
                    </button>
                </div>
            </div>
            <button @click="createProject" class="active">Done</button>
            <button @click="deleteProject" class="active">Delete</button>
        </div>
        <div v-if="edit != true">
            <h1 class="name">{{ projectName }}</h1>
