<script lang="js">
import { defineComponent } from 'vue';
import {createProj, deleteProj, getModels} from '../../userService.js';

export default defineComponent({
    name: 'Project',
    data() {
        return {
            cameraId: '',
            image: 'active',
            video: 'inactive',
            search: '',
            selected: '',
            selectedId: 0,
            projectName: '',
            projectDescription: '',
            models: [
            ],
            selectedModels: [
            ],
            hoveredIndex: null,
        };
    },
    props: {
        name: String,
        desc: String,
        type: Number,
        model: String,
        edit: Boolean,
        uuid: String
    },
    mounted() {
        this.projectName = this.name;
        this.projectDescription = this.desc;
        this.fetchModels()
    },
    components: {
    },
    methods: {
        showDescription(index) {
            this.hoveredIndex = index;
        },
        hideDescription(index) {
            this.hoveredIndex = null;
        },
        async fetchModels() {
            try {
                const res = await getModels();
                for (const model of res.data) {
                    this.models.push({
                        name: model.name,
                        desc: model.description,
                        selected: false,
                        id: model.uuid,
                    });
                    this.selectedModels.push({
                        name: model.name,
                        desc: model.description,
                        selected: false,
                        id: model.uuid,
                    });
                }
                console.log(this.model);
                for (var i = 0; i < this.selectedModels.length; i++) {
                    if (this.selectedModels[i].id == this.model) {
                        this.selectedModels[i].selected = 'true'
                        this.selected = this.selectedModels[i].name;
                        this.selectedId = this.selectedModels[i].id;
                    }
                }
            } catch (error) {
                this.incorrect = true;
                this.alert = error;
            }
        },
        createProject: function () {
            createProj(this.projectName, this.projectDescription, this.selectedId).then((res) => {
                console.log(res);
            })
            .catch((error) => {
                console.log(error)
            });
            this.$emit('edit', false);
        },
        deleteProject: function () {
            deleteProj(this.uuid).then((res) => {
                console.log(res);
            })
            .catch((error) => {
                console.log(error)
            });
        },
        changeType: function () {
            if (this.image == 'active') {
                this.image = 'inactive';
                this.video = 'active';
            } else {
                this.image = 'active';
                this.video = 'inactive';
            }
        },
        selectModel: function (index) {
            for (var i = 0; i < this.selectedModels.length; i++) {
                this.selectedModels[i].selected = 'false';
            }
            this.selectedModels[index].selected = 'true'
            this.selected = this.selectedModels[index].name;
            this.selectedId = this.selectedModels[index].id;
        },
        filterModels: function () {
            this.selectedModels = [];
            for (var i = 0; i < this.models.length; i++) {
                if (this.models[i].name.toLowerCase().includes(this.search.toLowerCase())) {
                    this.selectedModels.push(this.models[i]);
                }
            }
        },
        done: function () {
            this.$emit('edit', false);
            this.$emit('name', this.projectName);
            this.$emit('desc', this.projectDescription);
            this.$emit('type', this.image == 'active' ? 0 : 1);
            this.$emit('model', this.selected);
        },
        editCard: function () {
            this.$emit('edit', true);
        }
    }
})
</script>