<template>
    <div class="tryPage">
        <SelectModelTry v-on:selectedModel="selectModel"></SelectModelTry>
        <div class="upload">
            <label class="custom-file-upload">
                <input @change="newFile" type="file" />
                <i class="fa fa-cloud-upload"></i>
                {{$t('model.select')}}
            </label>
            <p class="file-name">{{file.name}}</p>
            <button class="submit-button" @click="runModel">{{$t('model.try')}}</button>
        </div>
        <img id="server-image" alt="">
    </div>
</template>
  
<style>

</style>
  
<script lang="js">
import { defineComponent } from 'vue';
import SelectModelTry from '../components/SelectModelTry';
import { tryModel } from '../cameraServices.js'

export default defineComponent({
    name: 'TryModel',
    data() {
        return {
            file: "",
            serverImg: "",
            chosenModel: ""
        };
    },
    mounted() {
    },
    components: {
        SelectModelTry
    },
    methods: {
        selectModel(ev) {
            this.chosenModel = ev;
        },
        newFile(e) {
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length)
                return;
            this.file = files[0];
        },
        runModel() {
            console.log(this.file)
            tryModel(this.file, this.chosenModel).then((res) => {
                this.serverImg = res.data; 
                console.log(res);
                document.getElementById('server-image').src = "data:image/jpeg;base64," + res.data;
            });
        }
    },
    mounted() {
        //fetch('http://localhost:3000/upload_files', {
        //    method: 'POST',
        //    body: formData
        //})
        //.then(resp => resp.json())
        //.then(data => {
        //    if (data.errors) {
        //        alert(data.errors)
        //    }
        //    else {
        //        console.log(data)
        //    }
        //})
    }
})
</script>

