<template>
  <div class="landing">
    <Hero></Hero>
    <FAQ></FAQ>
  </div>
</template>

<script>
import TeamInfo from '../components/LandingPage/TeamInfo';
import Presentation from '../components/LandingPage/Presentation';
import FAQ from './VueFaqAccordion';
import Hero from '../components/LandingPage/Hero';
import { getRole } from '../services/auth-service.js';

export default {
  mounted() {
    if (this.$route.query.token) {
      localStorage.setItem('accessToken', this.$route.query.token);
      this.$router.push('/');
      
      console.log("Google Login Complete");
      console.log('accessToken ' + localStorage.getItem("accessToken"));
      
      getRole();
    }
  },
  name: 'Launch',
  components: {
    TeamInfo,
    Presentation,
    FAQ,
    Hero
  }
}
</script>

<style scoped>
.landing {
  height: 100%;
}
