<template>
  <div class="models">
    <button class="add-model" @click="open">
      <p class="plus-sign">+</p>
      <p class="add-model-text">IMPORT YOUR MODEL</p>
    </button>
    <div v-for="(model, index) in models">
      <ModelCard :name="model.name" :desc="model.desc" :pic="''"></ModelCard>  
    </div>
  </div>
  <div class="upload" v-if="show">
    <button class="close" @click="close">x</button>
    <h1>Import Your Model</h1>
    <input type="text" placeholder="model name" v-model="name">
    <input type="text" placeholder="model description" v-model="descr">
    <input type="file">
    <button class="upload-model" @click="push">UPLOAD</button>
  </div>
</template>

<style scoped>
.close {
  position: absolute;
  top: 2%;
  left: 2%;
  font-size: 20px;
}
input {
  border: 1px solid black
}
.upload-model {
  background-color: #0049b0;
