}
.upload {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 20%;
  height: 40%;
  background-color: white;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateY(-50%);
  transform: translateX(-50%);
  border: 1px solid black;
  min-width: fit-content;
}
.models {
  gap: 50px;
  flex-wrap: wrap;
  display: flex;
  margin-top: 80px;
  margin-left: 2%;
}

