<script lang="js">
import { defineComponent } from 'vue';
import { redditAuth } from '../logServices.js';
import { getModels } from '../userService.js';
import ModelCard from '../components/ModelCard';
import SelectModel from '../components/SelectModel';

export default defineComponent({
  name: 'ModelsPage',
  data() {
    return {
      redditClient: '',
      redditSecret: '',
      models: [],
      show: false,
    };
  },
  mounted() {
    this.getModels()
  },
  components: {
    ModelCard,
    SelectModel
  },
  methods: {
    auth() {
      localStorage.setItem("redditClient", this.redditClient);
      localStorage.setItem("redditSecret", this.redditSecret);
      window.open(`https://www.reddit.com/api/v1/authorize?
							client_id=${this.redditClient}&response_type=code
							&state=random-string
							&redirect_uri=http://localhost:8081/reddit/callback
							&duration=permanent&scope=identity,submit,save`,
            "_self");
    },
    async getModels() {
      const res = await getModels();
        for (const model of res.data) {
          this.models.push({
              name: model.name,
              desc: model.description,
              id: model.uuid,
          });
        }
    },
    open() {
      this.show = true;
    },
    close() {
      this.show = false;
    },
    push() {
      this.models.push({ name: this.name,
              desc: this.descr,
              id: "",});
      this.close();
    }
  }
})
</script>