<template>
    <div class="faqs">
      <FAQ
        v-for="(faq, i) in faqs"
        :faq="faq"
        :index="i"
        :key="i"
        :open="faq.open"
        @toggleOpen="toggleOpen"
      />
    </div>
</template>

<script>
import FAQ from "./FAQ";

export default {
  name: "VueFaq",
  components: {
    FAQ,
  },
  data() {
    return {
      faqs: [
        {
          question: "What is computer vision?",
          answer:
            "Computer vision is a field of study that focuses on enabling machines to see, understand, and process images or videos in the same way as humans.",
          open: false,
        },
        {
          question: "What are the applications of computer vision?",
          answer:
            "Computer vision is used in a wide range of applications, such as facial recognition, object detection, motion tracking, augmented reality, autonomous vehicles, medical image analysis, industrial quality control, among others.",
          open: false,
        },
        {
          question: "What are the main challenges in computer vision?",
          answer:
            "Some common challenges in computer vision include accurate detection and recognition of objects under different lighting conditions, scale variations, perspective changes, and complex backgrounds.",
          open: false,
        },
        {
          question: "What techniques are used in computer vision?",
          answer:
            "In computer vision, various techniques are used, such as image processing, machine learning, convolutional neural networks (CNN), pattern recognition, 3D modeling, image segmentation, and feature extraction.",
          open: false,
        },
        {
          question: "What is deep learning in computer vision?",
          answer:
            "Deep learning, also known as deep neural networks, is a machine learning technique that uses artificial neural networks with multiple layers to learn and extract relevant features from images, which has led to significant advancements in computer vision.",
          open: false,
        },
        {
          question: "What are the benefits of computer vision in industry?",
          answer:
            "Computer vision can improve efficiency, accuracy, and safety in numerous industries, such as manufacturing, agriculture, medicine, transportation, security, retail, and industrial automation.",
          open: false,
        },
        {
          question: "What are the current limitations of computer vision?",
          answer:
            "Although computer vision has made significant progress, there are still challenges in areas such as real-time object recognition, contextual understanding of complex images and videos, emotion recognition, and privacy in surveillance applications.",
          open: false,
        },
      ],
    };
  },
  methods: {
    toggleOpen: function (index) {
      this.faqs = this.faqs.map((faq, i) => {
        if (index === i) {
          faq.open = !faq.open;
        } else {
          faq.open = false;
        }

        return faq;
      });
    },
  },
};
</script>

