import axios from 'axios';
import http from "./http-requests-area";

export async function getUserInfo() {
    let token = localStorage.getItem("token")
    return await axios({
        method: 'GET',
        url: 'http://localhost:8000/users/info',
        headers: {
            'accept': "application/json",
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
        }
    });
}

export async function createProj(name, desc, model) {
    return await http.post('/v1/projects', {
        name: name,
        description: desc,
        models_uuid: [model]
    }, { headers: {
        'Authorization': `Bearer ${localStorage.getItem("access_token")}`,
        'Content-Type': 'application/json',
    }});
}

export async function deleteProj(uuid) {
    return await http.delete(`/v1/projects/${uuid}/delete`,{ headers: {
        'Authorization': `Bearer ${localStorage.getItem("access_token")}`,
        'Content-Type': 'application/json',
    }});
}

export async function getProj() {
    return await http.get(`/v1/projects/`, { headers: {
        'Authorization': `Bearer ${localStorage.getItem("access_token")}`,
        'accept': 'application/json',
    }});
}

export async function getModels() {
    return await http.get(`/v1/models/`, { headers: {
        'Authorization': `Bearer ${localStorage.getItem("access_token")}`,
        'accept': 'application/json',
    }});
}
